"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { CalendarIcon, BookOpenIcon, PlayIcon } from "lucide-react"

interface ContentCardProps {
  title: string
  summary: string
  type: 'article' | 'video'
  url: string
  date: string
  source: string
}

export function ContentCard({ title, summary, type, url, date, source }: ContentCardProps) {
  return (
    <Card className="hover:shadow-lg transition-shadow duration-200">
      <CardHeader>
        <div className="flex items-center justify-between">
          <Badge variant={type === 'article' ? 'default' : 'secondary'}>
            <span className="flex items-center gap-1">
              {type === 'article' ? <BookOpenIcon className="w-3 h-3" /> : <PlayIcon className="w-3 h-3" />}
              {type}
            </span>
          </Badge>
          <div className="flex items-center text-sm text-gray-500">
            <CalendarIcon className="w-4 h-4 mr-1" />
            {date}
          </div>
        </div>
        <CardTitle className="mt-2 text-xl">
          <a 
            href={url}
            target="_blank"
            rel="noopener noreferrer"
            className="hover:text-blue-600 transition-colors duration-200"
          >
            {title}
          </a>
        </CardTitle>
        <CardDescription>{source}</CardDescription>
      </CardHeader>
      <CardContent>
        <p className="text-gray-600">{summary}</p>
      </CardContent>
    </Card>
  )
}
